import React from 'react'; 
import './App.css';
import MovieRow from './components/movierow';
import $ from 'jquery'

class App extends React.Component {
  constructor(props){
    super(props)
    this.state= {}
   // console.log("this is my initializer")

      /*
       const movies = [
      {id: 0, poster_src:"https://i.pinimg.com/originals/f9/90/12/f99012184e6ccc9769c02958c15bc38c.jpg", title: "Avengers: Infinity War", 
      overview: "Iron Man, Thor, the Hulk and the rest of the Avengers unite to battle their most powerful enemy yet -- the evil Thanos."},
      {id: 1, poster_src:"https://www.radtkesports.com/wp-content/uploads/1636_300_sleeavengers2sidedposter_1.png", title: "  The Avengers",
       overview: "This is my second overview"},

    ]

    
   
      var movieRows = []
      movies.forEach((movie) => {
        console.log(movie.title)

        const movieRow = <MovieRow  movie={movie}/>
        
        
        
        movieRows.push(movieRow)
      })
      this.state = {rows: movieRows}
      */
      this.performSearch("avengers")

  }

  performSearch(searchTerm) {
    console.log("Perform search using moviedb")
    const urlString = "https://api.themoviedb.org/3/search/movie?api_key=1b5adf76a72a13bad99b8fc0c68cb085&query=" + searchTerm
    $.ajax({
      url: urlString,
      success: (searchResults) => {
        console.log("Fetched data successfully")
        // console.log(searchResults)
        const results = searchResults.results
        // console.log(results[0])

        var movieRows = []

        results.forEach((movie) => {
          movie.poster_src = "https://image.tmdb.org/t/p/w185" + movie.poster_path
          // console.log(movie.poster_path)
          const movieRow = <MovieRow key={movie.id} movie={movie}/>
          movieRows.push(movieRow)
        })

        this.setState({rows: movieRows})
      },
      error: (xhr, status, err) => {
        console.error("Failed to fetch data")
      }
    })
  }

  searchChangeHandler(event) {
    console.log(event.target.value)
    const boundObject = this
    const searchTerm = event.target.value
    boundObject.performSearch(searchTerm)
  }

  render() {
    return (
      <div>
        
        <table className="titleBar">
          <tbody>
            <tr>
              <td>
                <img alt="app icon" width="50" src="green_app_icon.svg"/>
              </td>
              <td width="8"/>
              <td>
                <h1>MoviesDB Search</h1>
              </td>
            </tr>
          </tbody>
        </table>

        <input style={{
          fontSize: 24,
          display: 'block',
          width: "99%",
          paddingTop: 8,
          paddingBottom: 8,
          paddingLeft: 16
        }} onChange={this.searchChangeHandler.bind(this)} placeholder="Enter search term"/>

        {this.state.rows}

      </div>
    );
  }
}

export default App;